import React, { useEffect, useState, useContext, useCallback } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import { CartContext } from "../pages/CartContext"; // Context  qun l gi hng
import { AuthContext } from "../account/AuthContext"; // Context  qun l trng thi ng nhp
import "./ProductDetail.css"; // File CSS  nh dng giao din chi tit sn phm

// nh ngha cc hng s
const API_URL = `${process.env.PUBLIC_URL}/db.json`; // ng dn ti file JSON cha d liu sn phm
const MESSAGES = {
  LOADING: " ang ti...", // Thng bo khi d liu ang c ti
  ERROR_FETCH: "Khng th ti d liu sn phm!", // Thng bo khi fetch d liu tht bi
  ERROR_NOT_FOUND: "Sn phm khng tn ti!", // Thng bo khi khng tm thy sn phm theo id
  SUCCESS_ADD_TO_CART: " Thm vo gi hng thnh cng!", // Thng bo khi thm sn phm vo gi thnh cng
  LOGIN_REQUIRED: "Vui lng ng nhp  tip tc!", // Thng bo khi ngi dng cha ng nhp
};

// Component chnh hin th chi tit sn phm
const ProductDetail = () => {
  const { id } = useParams(); // Ly ID sn phm t tham s URL
  const navigate = useNavigate(); // Hook  iu hng ngi dng n cc trang khc
  const { addToCart } = useContext(CartContext); // Ly hm thm sn phm vo gi t CartContext
  const { isLoggedIn = false } = useContext(AuthContext) || {}; // Ly trng thi ng nhp, mc nh l false nu khng c AuthContext

  const [product, setProduct] = useState(null); // State lu thng tin sn phm, khi to l null
  const [isLoading, setIsLoading] = useState(true); // State kim sot trng thi ang ti d liu
  const [error, setError] = useState(null); // State lu thng bo li nu c
  const [successMessage, setSuccessMessage] = useState(""); // State lu thng bo thnh cng hoc yu cu ng nhp

  // Fetch d liu sn phm khi component mount hoc id thay i
  useEffect(() => {
    const controller = new AbortController(); // To AbortController  hy yu cu fetch nu cn

    const fetchProduct = async () => {
      try {
        setIsLoading(true); // Bt trng thi ang ti
        setError(null); // Xa thng bo li c
        const response = await fetch(API_URL, { signal: controller.signal }); // Gi yu cu ly d liu t API
        if (!response.ok) throw new Error(MESSAGES.ERROR_FETCH); // Nm li nu fetch khng thnh cng

        const data = await response.json(); // Chuyn d liu t JSON sang object JavaScript
        const foundProduct = data.products?.find((p) => p.id === Number(id)); // Tm sn phm khp vi id t URL
        if (!foundProduct) throw new Error(MESSAGES.ERROR_NOT_FOUND); // Nm li nu khng tm thy sn phm

        setProduct(foundProduct); // Lu thng tin sn phm vo state
      } catch (err) {
        if (err.name !== "AbortError") {
          setError(err.message); // Lu thng bo li nu khng phi li do hy fetch
        }
      } finally {
        setIsLoading(false); // Tt trng thi ang ti d thnh cng hay tht bi
      }
    };

    fetchProduct(); // Gi hm fetch d liu
    return () => controller.abort(); // Cleanup: Hy fetch khi component unmount hoc id thay i
  }, [id]); // Dependency: chy li khi id thay i

  // X l thm sn phm vo gi hng, ti u vi useCallback
  const handleAddToCart = useCallback(() => {
    if (!isLoggedIn) {
      setSuccessMessage(MESSAGES.LOGIN_REQUIRED); // Hin th thng bo yu cu ng nhp nu cha ng nhp
      setTimeout(() => navigate("/login"), 1000); // Chuyn hng n trang ng nhp sau 1 giy
      return;
    }

    if (!product) return; // Khng lm g nu khng c sn phm

    addToCart(product); // Thm sn phm vo gi hng qua CartContext
    setSuccessMessage(MESSAGES.SUCCESS_ADD_TO_CART); // Hin th thng bo thnh cng

    const timer = setTimeout(() => {
      setSuccessMessage(""); // Xa thng bo sau 1 giy
      navigate("/cart"); // Chuyn hng n trang gi hng
    }, 1000);

    return () => clearTimeout(timer); // Cleanup: Xa timer nu component unmount
  }, [product, addToCart, isLoggedIn, navigate]); // Dependencies: chy li khi cc gi tr ny thay i

  // Hin th trng thi ang ti
  if (isLoading) {
    return <p className="loading">{MESSAGES.LOADING}</p>; // Thng bo ang ti d liu
  }

  // Hin th trng thi li
  if (error) {
    return <p className="error"> {error}</p>; // Hin th thng bo li nu c
  }

  // Hin th nu khng c sn phm
  if (!product) {
    return <p className="warning"> Khng c d liu sn phm</p>; // Thng bo khi khng c sn phm
  }

  // Giao din chnh ca trang chi tit sn phm
  return (
    <div className="product-detail">
      <section className="product-content">
        <h2>{product.name}</h2> {/* Hin th tn sn phm */}
        <img
          src={product.image} // ng dn nh sn phm
          alt={product.name} // Vn bn thay th cho nh (accessibility)
          className="product-image" // Class CSS  nh dng nh
          loading="lazy" // Ti nh theo kiu lazy  ti u hiu nng
        />
        <div className="price-section">
          <p className="price">
             {product.price.toLocaleString("vi-VN")} VN
          </p> {/* Hin th gi sn phm, nh dng tin Vit Nam */}
        </div>
        <p className="description">{product.description}</p> {/* Hin th m t sn phm */}

        <div className="specs">
          <h3> Thng s k thut</h3> {/* Tiu  phn thng s k thut */}
          <ul>
            <li> Mn hnh: {product.screen}</li> {/* Thng s mn hnh */}
            <li> Chip: {product.chip}</li> {/* Thng s chip */}
            <li> RAM: {product.ram}</li> {/* Thng s RAM */}
            <li> B nh: {product.storage}</li> {/* Thng s b nh */}
            <li> Camera: {product.camera}</li> {/* Thng s camera */}
            <li> Pin: {product.battery}</li> {/* Thng s pin */}
          </ul>
        </div>

        {successMessage && (
          <p className="success-message">{successMessage}</p>
          // Hin th thng bo thnh cng hoc yu cu ng nhp nu c
        )}
      </section>

      <div className="button-group">
        <button
          className="add-to-cart" // Class CSS cho nt thm vo gi
          onClick={handleAddToCart} // Gi hm thm vo gi khi nhn
          disabled={!product} // V hiu ha nt nu khng c sn phm
        >
           Thm vo gi {/* Nt thm sn phm vo gi hng */}
        </button>
        <Link to="/home" className="back-button">
           Quay li {/* Lin kt quay li trang ch */}
        </Link>
      </div>
    </div>
  );
};

export default ProductDetail; // Xut component  s dng  ni khc