import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../account/AuthContext";
import "./Account.css";

// nh ngha cc key dng cho localStorage
const LOCAL_STORAGE_KEYS = {
  USERS: "users",
  CURRENT_USER: "currentUser",
};

// nh ngha cc thng bo c nh
const MESSAGES = {
  EMPTY_FIELDS: "Vui lng nhp y  tn ng nhp v mt khu!",
  USER_EXISTS: "Tn ng nhp  tn ti!",
  REGISTER_SUCCESS: "ng k thnh cng! Hy ng nhp.",
  LOGIN_SUCCESS: "ng nhp thnh cng!",
  LOGIN_FAILED: "Sai thng tin ng nhp",
  LOGOUT_SUCCESS: "ng xut thnh cng!",
};

const Account = () => {
  const navigate = useNavigate();
  // Ly trng thi ng nhp v cc hm t AuthContext, mc nh nu khng c context
  const { isLoggedIn, login, logout } = useContext(AuthContext) || {
    isLoggedIn: false,
    login: () => {},
    logout: () => {},
  };

  const [isRegistering, setIsRegistering] = useState(false);
  const [formData, setFormData] = useState({ username: "", password: "" });
  const [message, setMessage] = useState("");

  // iu hng n trang ch nu  ng nhp
  useEffect(() => {
    if (isLoggedIn) {
      navigate("/home");
    }
  }, [isLoggedIn, navigate]);

  // X l thay i gi tr input trong form
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    setMessage("");
  };

  // X l ng k ti khon mi
  const handleRegister = () => {
    const { username, password } = formData;
    if (!username.trim() || !password.trim()) {
      setMessage(MESSAGES.EMPTY_FIELDS);
      return;
    }

    const storedUsers = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEYS.USERS)) || [];
    if (storedUsers.some((u) => u.username === username)) {
      setMessage(MESSAGES.USER_EXISTS);
      return;
    }

    const updatedUsers = [...storedUsers, { username, password }];
    localStorage.setItem(LOCAL_STORAGE_KEYS.USERS, JSON.stringify(updatedUsers));
    setMessage(MESSAGES.REGISTER_SUCCESS);
    setFormData({ username: "", password: "" });
    setTimeout(() => setIsRegistering(false), 1000);
  };

  // X l ng nhp
  const handleLogin = () => {
    const { username, password } = formData;
    if (!username.trim() || !password.trim()) {
      setMessage(MESSAGES.EMPTY_FIELDS);
      return;
    }

    const storedUsers = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEYS.USERS)) || [];
    const foundUser = storedUsers.find(
      (u) => u.username === username && u.password === password
    );

    if (foundUser) {
      login(foundUser);
      setMessage(MESSAGES.LOGIN_SUCCESS);
    } else {
      setMessage(MESSAGES.LOGIN_FAILED);
    }
  };

  // X l ng xut
  const handleLogout = () => {
    logout();
    setMessage(MESSAGES.LOGOUT_SUCCESS);
    setFormData({ username: "", password: "" });
    setTimeout(() => navigate("/"), 1000);
  };

  return (
    <div className="account-container">
      <div className="account-box">
        {/* Tiu  thay i ty trng thi ng nhp */}
        <h1>
          {isLoggedIn
            ? `Xin cho, ${formData.username || "Ngi dng"}!`
            : "ng nhp / ng k"}
        </h1>

        {isLoggedIn ? (
          <div>
            <p>Bn  ng nhp thnh cng!</p>
            <button
              className="account-button logout-btn"
              onClick={handleLogout}
              aria-label="ng xut"
            >
              ng xut
            </button>
            {/* Hin th thng bo nu c */}
            {message && (
              <p
                className={`login-message ${
                  message.includes("thnh cng") ? "success" : ""
                }`}
              >
                {message}
              </p>
            )}
          </div>
        ) : (
          <div>
            {/* Input cho tn ng nhp */}
            <input
              type="text"
              name="username"
              placeholder="Tn ng nhp"
              className="account-input"
              value={formData.username}
              onChange={handleChange}
              aria-label="Nhp tn ng nhp"
            />
            {/* Input cho mt khu */}
            <input
              type="password"
              name="password"
              placeholder="Mt khu"
              className="account-input"
              value={formData.password}
              onChange={handleChange}
              aria-label="Nhp mt khu"
            />
            {/* Hin th thng bo nu c */}
            {message && (
              <p
                className={`login-message ${
                  message.includes("thnh cng") ? "success" : ""
                }`}
              >
                {message}
              </p>
            )}
            {/* Nt iu khin ty ch  ng nhp/ng k */}
            <div className="account-buttons">
              {isRegistering ? (
                <>
                  <button
                    className="account-button register-btn"
                    onClick={handleRegister}
                    aria-label="ng k ti khon"
                  >
                    ng k
                  </button>
                  <button
                    className="link-to-home"
                    onClick={() => setIsRegistering(false)}
                    aria-label="Quay li ng nhp"
                  >
                    Quay li ng nhp
                  </button>
                </>
              ) : (
                <>
                  <button
                    className="account-button login-btn"
                    onClick={handleLogin}
                    aria-label="ng nhp"
                  >
                    ng nhp
                  </button>
                  <button
                    className="link-to-home"
                    onClick={() => setIsRegistering(true)}
                    aria-label="Chuyn sang ng k"
                  >
                    Cha c ti khon? ng k
                  </button>
                </>
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Account;