import React, { useContext } from "react";
import { HashRouter as Router, Routes, Route, Navigate } from "react-router-dom";
// Import cc page v component t th mc tng ng
import ProductPage from "./pages/ProductPage"; // Trang danh sch sn phm
import ProductDetail from "./components/ProductDetail"; // Trang chi tit sn phm (chuyn t components sang pages)
import Account from "./account/Account"; // Trang ng nhp/ng k (chuyn t account sang pages)
import CartPage from "./pages/CartPage"; // Trang gi hng
import OrderHistory from "./pages/OrderHistory";
import { AuthContext } from "./account/AuthContext"; // Context qun l trng thi xc thc
import { AuthProvider } from "./account/AuthContext"; // Provider cho AuthContext
import { CartProvider } from "./pages/CartContext"; // Provider cho CartContext
import Footer from "./components/Footer"; // Component Footer chung
import Header from "./components/Header";
// Component ProtectedRoute - Bo v cc route yu cu ng nhp
const ProtectedRoute = ({ children }) => {
  const authContext = useContext(AuthContext); // Ly AuthContext t useContext
  const { isLoggedIn } = authContext || { isLoggedIn: false }; // Gii cu trc isLoggedIn, mc nh false nu khng c context

  if (!isLoggedIn) {
    // Nu cha ng nhp, chuyn hng v trang ng nhp ("/")
    return <Navigate to="/" replace />; // replace  khng lu lch s iu hng
  }

  // Nu  ng nhp, render cc component con
  return children;
};

// Component App - nh ngha cu trc ng dng v routing
const App = () => {
  return (
    <AuthProvider>
      <CartProvider>
        <Router>
          <Header /> {/* Thm Header vo y */}
          <Routes>
            <Route path="/" element={<Account />} />
            <Route
              path="/home"
              element={
                <ProtectedRoute>
                  <ProductPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/products/:id"
              element={
                <ProtectedRoute>
                  <ProductDetail />
                </ProtectedRoute>
              }
            />
            <Route
              path="/cart"
              element={
                <ProtectedRoute>
                  <CartPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/orders"
              element={
                <ProtectedRoute>
                  <OrderHistory />
                </ProtectedRoute>
              }
            />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
          <Footer />
        </Router>
      </CartProvider>
    </AuthProvider>
  );
};

export default App; // Xut component App  s dng lm root ca ng dng