import React, { useState, useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import { CartContext } from "../pages/CartContext";
import { AuthContext } from "../account/AuthContext";
import UserProfileModal from "./UserProfileModal";
import "./Header.css";

// Component Header
const Header = () => {
  // Contexts v Hooks
  const { cart } = useContext(CartContext);
  const { user, isLoggedIn, logout } = useContext(AuthContext);
  const navigate = useNavigate();
  const [isProfileModalOpen, setIsProfileModalOpen] = useState(false);

  // X l ng xut
  const handleLogout = () => {
    logout();
    navigate("/");
  };

  // Ly s lng sn phm trong gi hng
  const getCartItemCount = () => cart.length || 0;

  // Ly tn hin th ca ngi dng
  const getDisplayName = () => user?.username || "Khch";

  return (
    <header className="header">
      <Link to="/home" className="store-title">
         MobileStore
      </Link>

      <div className="header-actions">
        {isLoggedIn ? (
          <>
            <button
              className="profile-button"
              onClick={() => setIsProfileModalOpen(true)}
              aria-label="M thng tin ngi dng"
            >
               Xin Cho {getDisplayName()}
            </button>

            <button
              className="logout-button"
              onClick={handleLogout}
              aria-label="ng xut"
            >
               ng xut
            </button>

            <Link
              to="/cart"
              className="cart-button"
              aria-label="Xem gi hng"
            >
               Gi hng ({getCartItemCount()})
            </Link>
          </>
        ) : (
          <Link
            to="/"
            className="login-button"
            aria-label="ng nhp"
          >
             ng nhp
          </Link>
        )}
      </div>

      {isProfileModalOpen && (
        <UserProfileModal onClose={() => setIsProfileModalOpen(false)} />
      )}
    </header>
  );
};

export default Header;