import React, { useState } from "react";
import "./CheckoutModal.css";

// Component CheckoutModal - Hin th modal xc nhn thanh ton
const CheckoutModal = ({ cart, totalPrice, onConfirm, onCancel }) => {
  const [shippingInfo, setShippingInfo] = useState({
    name: "", // H v tn ngi nhn
    address: "", // a ch giao hng
    phone: "", // S in thoi lin h
  });
  const [validationErrors, setValidationErrors] = useState({}); // State lu li xc thc form

  // X l thay i input
  const handleChange = (e) => {
    const { name, value } = e.target;
    setShippingInfo((prev) => ({ ...prev, [name]: value })); // Cp nht thng tin giao hng
    if (validationErrors[name]) {
      setValidationErrors((prev) => ({ ...prev, [name]: "" })); // Xa li nu ngi dng nhp li
    }
  };

  // Kim tra tnh hp l ca form
  const validateForm = () => {
    const errors = {};
    const { name, address, phone } = shippingInfo;

    if (!name.trim()) errors.name = "Vui lng nhp h v tn"; // Kim tra h tn rng
    if (!address.trim()) errors.address = "Vui lng nhp a ch giao hng"; // Kim tra a ch rng
    if (!phone.trim()) {
      errors.phone = "Vui lng nhp s in thoi"; // Kim tra s in thoi rng
    } else if (!/^(0|\+84)[3|5|7|8|9][0-9]{8}$/.test(phone)) {
      errors.phone = "S in thoi khng hp l"; // Kim tra nh dng s in thoi VN
    }

    setValidationErrors(errors); // Cp nht li vo state
    return Object.keys(errors).length === 0; // Tr v true nu khng c li
  };

  // X l xc nhn n hng
  const handleSubmit = (e) => {
    e.preventDefault(); // Ngn chn form submit mc nh
    if (validateForm()) {
      onConfirm(shippingInfo); // Gi hm onConfirm vi thng tin giao hng nu form hp l
    }
  };

  // Giao din modal
  return (
    <div className="modal-overlay" onClick={onCancel}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <h2 className="modal-title"> Xc nhn thanh ton</h2> {/* Tiu  modal */}

        <div className="order-summary">
          <h3> Thng tin n hng</h3> {/* Tiu  phn thng tin n hng */}
          {cart.length > 0 ? (
            <>
              <ul className="cart-items-list">
                {cart.map((item) => (
                  <li key={item.id} className="cart-item">
                    <span className="item-name">{item.name}</span> {/* Tn sn phm */}
                    <span className="item-quantity">x {item.quantity}</span> {/* S lng */}
                    <span className="item-price">
                      {(item.price * item.quantity).toLocaleString("vi-VN")} VN {/* Tng gi tng sn phm */}
                    </span>
                  </li>
                ))}
              </ul>
              <p className="total-price">
                <strong>Tng tin:</strong> {totalPrice.toLocaleString("vi-VN")} VN {/* Tng tin n hng */}
              </p>
            </>
          ) : (
            <p className="empty-cart-message">Khng c sn phm trong gi hng</p> // Thng bo nu gi hng rng
          )}
        </div>

        <form onSubmit={handleSubmit} className="shipping-form">
          <h3> Thng tin giao hng</h3> {/* Tiu  phn thng tin giao hng */}

          <div className="form-group">
            <label htmlFor="name">H v tn:</label>
            <input
              type="text"
              id="name"
              name="name"
              placeholder="Nhp h v tn ngi nhn"
              value={shippingInfo.name}
              onChange={handleChange}
              className={validationErrors.name ? "error" : ""} // Thm class error nu c li
            />
            {validationErrors.name && (
              <span className="error-message">{validationErrors.name}</span> // Hin th li nu c
            )}
          </div>

          <div className="form-group">
            <label htmlFor="address">a ch:</label>
            <input
              type="text"
              id="address"
              name="address"
              placeholder="Nhp a ch giao hng chi tit"
              value={shippingInfo.address}
              onChange={handleChange}
              className={validationErrors.address ? "error" : ""} // Thm class error nu c li
            />
            {validationErrors.address && (
              <span className="error-message">{validationErrors.address}</span> // Hin th li nu c
            )}
          </div>

          <div className="form-group">
            <label htmlFor="phone">S in thoi:</label>
            <input
              type="tel"
              id="phone"
              name="phone"
              placeholder="Nhp s in thoi lin h"
              value={shippingInfo.phone}
              onChange={handleChange}
              className={validationErrors.phone ? "error" : ""} // Thm class error nu c li
            />
            {validationErrors.phone && (
              <span className="error-message">{validationErrors.phone}</span> // Hin th li nu c
            )}
          </div>

          <div className="modal-buttons">
            <button type="submit" className="confirm-button">
               Xc nhn t hng {/* Nt xc nhn */}
            </button>
            <button
              type="button"
              className="cancel-button"
              onClick={onCancel} // ng modal khi nhn
            >
               Hy {/* Nt hy */}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CheckoutModal; // Xut component  s dng  ni khc