import React, { createContext, useState, useEffect } from "react";

// nh ngha key dng cho localStorage
const LOCAL_STORAGE_KEY = "currentUser";

// Gi tr mc nh cho AuthContext
const defaultAuthContext = {
  isLoggedIn: false,
  user: null,
  login: () => {},
  logout: () => {},
};

// To AuthContext vi gi tr mc nh
export const AuthContext = createContext(defaultAuthContext);

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [user, setUser] = useState(null);

  // Khi to trng thi ng nhp khi component mount
  useEffect(() => {
    const savedUser = localStorage.getItem(LOCAL_STORAGE_KEY);
    if (savedUser) {
      const parsedUser = JSON.parse(savedUser);
      setIsLoggedIn(true);
      setUser(parsedUser);
    }
  }, []);

  // X l ng nhp: lu thng tin ngi dng v cp nht trng thi
  const login = (userData) => {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(userData));
    setIsLoggedIn(true);
    setUser(userData);
  };

  // X l ng xut: xa thng tin v t li trng thi
  const logout = () => {
    localStorage.removeItem(LOCAL_STORAGE_KEY);
    setIsLoggedIn(false);
    setUser(null);
  };

  // Gi tr cung cp qua Context
  const authContextValue = {
    isLoggedIn,
    user,
    login,
    logout,
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;