import React, { useState, useContext } from "react";
import { AuthContext } from "../account/AuthContext";
import "./UserProfileModal.css";

const UserProfileModal = ({ onClose }) => {
  // Ly thng tin ngi dng v hm logout t AuthContext
  const { user, logout } = useContext(AuthContext);

  // State  qun l mt khu c, mt khu mi, mt khu xc nhn v thng bo
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [message, setMessage] = useState("");

  // Hm x l khi ngi dng submit form i mt khu
  const handleChangePassword = (e) => {
    e.preventDefault();

    // Kim tra mt khu c c khp vi mt khu hin ti khng
    if (oldPassword !== user.password) {
      setMessage("Mt khu c khng ng!");
      return;
    }

    // Kim tra mt khu xc nhn c khp vi mt khu mi khng
    if (newPassword !== confirmPassword) {
      setMessage("Mt khu xc nhn khng khp!");
      return;
    }

    // Kim tra  di ti thiu ca mt khu mi
    if (newPassword.length < 6) {
      setMessage("Mt khu mi phi c t nht 6 k t!");
      return;
    }

    // Cp nht mt khu trong localStorage
    const storedUsers = JSON.parse(localStorage.getItem("users")) || [];
    const updatedUsers = storedUsers.map((storedUser) =>
      storedUser.username === user.username
        ? { ...storedUser, password: newPassword }
        : storedUser
    );

    // Lu danh sch ngi dng  cp nht v thng tin ngi dng hin ti vo localStorage
    localStorage.setItem("users", JSON.stringify(updatedUsers));
    localStorage.setItem(
      "currentUser",
      JSON.stringify({ ...user, password: newPassword })
    );

    // Hin th thng bo thnh cng v reset form
    setMessage("i mt khu thnh cng! Vui lng ng nhp li.");
    setOldPassword("");
    setNewPassword("");
    setConfirmPassword("");

    // T ng ng xut v ng modal sau 2 giy
    setTimeout(() => {
      logout();
      onClose();
    }, 2000);
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2>Thng tin ngi dng</h2>
        {/* Hin th thng tin ngi dng, mc nh nu khng c d liu */}
        <div className="user-info">
          <p>
            <strong>Tn ng nhp:</strong>{" "}
            {user?.username || "Khng c d liu"}
          </p>
        </div>

        <h3>i mt khu</h3>
        {/* Form  nhp mt khu c, mt khu mi v xc nhn mt khu */}
        <form onSubmit={handleChangePassword}>
          <input
            type="password"
            placeholder="Mt khu c"
            value={oldPassword}
            onChange={(e) => setOldPassword(e.target.value)}
            required
            aria-label="Nhp mt khu c"
          />
          <input
            type="password"
            placeholder="Mt khu mi"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            required
            aria-label="Nhp mt khu mi"
          />
          <input
            type="password"
            placeholder="Xc nhn mt khu"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
            aria-label="Xc nhn mt khu mi"
          />

          {/* Hin th thng bo li hoc thnh cng */}
          {message && (
            <p className={message.includes("thnh cng") ? "success" : "error"}>
              {message}
            </p>
          )}

          {/* Cc nt iu khin trong modal */}
          <div className="modal-buttons">
            <button type="submit" className="confirm-button">
              i mt khu
            </button>
            <button
              type="button"
              className="cancel-button"
              onClick={onClose}
              aria-label="ng modal"
            >
              ng
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default UserProfileModal;